import Swiper, { Navigation, Pagination } from 'swiper';
let swiper = [{}];
swiper[0].destroyed = true;

function initialiseSwiper() {
    swiper = new Swiper('.swiper', {
        modules: [Navigation, Pagination],
        direction: 'horizontal', //   Swiper
        loop: true,  //  Swiper  (Swiper        .swiper-wrapper    "" )
        slidesPerView: 'auto', //     ,    -     .swiper-wrapper
        pagination: { //  
            el: '.swiper-pagination', //   
            clickable: true //  ,    bullets 
        },
        init: true

    })
    console.log(swiper);

}


window.onload = () => {  //    
    if (window.matchMedia('(max-width: 767px)').matches) { // //     ,      767px
        initialiseSwiper()
    }
}


window.addEventListener('resize', () => {
    let isNeed = window.matchMedia('(max-width: 767px)').matches,
        isDesrtoyed = swiper[0].destroyed;
    if (isNeed && isDesrtoyed) {
        initialiseSwiper();
    } else if (!isNeed && !isDesrtoyed) {
        destroySwiper()
    }

})

const destroySwiper = function () {
    for (let s of swiper) {
        s.destroy(true, true);
    }

}
